<template>
  <div class="shopping-section">

    <div class="cart-btn" @click="showCart()">
      <span class="nav-icon">
        <font-awesome-icon class="cart-icon" icon="cart-plus"></font-awesome-icon>
      </span>
      <div class="cart-items">{{totalItems}}</div>
    </div>

    <!-- HEADER -->
      <header class="hero">
        <div class="banner">
          <h1 class="banner-title">Forniture collection</h1>
          <button class="banner-btn">shop now</button>
        </div>
      </header>
    <!-- HEADER -->

    <!-- PRODUCTS -->
      <section class="products">
        <div class="section-title">
          <h2>PRODUCTS</h2>
        </div>
        <div class="products-center">
          <article class="product" v-for="(p, index) in productsList" :key="index.key">
            <product
            :product="p"
            :indexProduct="index"
            @selected="selectedProduct">
            </product>
          </article>
        </div>
      </section>
    <!-- PRODUCTS -->

    <!-- FOOTER -->
    <footer class="footer">
      <div class="footer-center">
        <section class="rights-reserved">
          <h5 class="rights-reserved__description "> COLLECTION, 2019. ALL RIGHTS RESERVED
          </h5>
        </section>
      </div>
    </footer>
    <!-- FOOTER -->
    <!-- cart -->
      <div :class="['cart-overlay', {'transparentBcg' : showProp}]">
        <div :class="['cart', {'showCart' : showProp}]">
          <span class="close-cart" @click="closeCart()">
            <font-awesome-icon icon="times"></font-awesome-icon>
          </span>
          <h2>your cart</h2>
          <div class="cart-content">
            <!-- cart-item -->
            <div class="cart-product" v-for="(cart, indexCart) in productsBag.products"
            :key="indexCart.key">
            <!-- <template v-for="value in productsInCart.products.id.length"></template> -->
              <cart
              :cartObject="cart"
              :cartIndexObject="indexCart"
              @increaseItem="addNewItem"
              @reduceItem="removeAnItem">
              <!-- @deleteItemInCart="deleteItem" -->
              <!-- :itemsInCartById="value" -->
              </cart>
            </div>
            <!-- end cart-item -->
            <div class="cart-footer">
              <h3>total in cart : <span class="cart-total">{{totalItems}}</span><span style="color: white;">&nbsp;items</span></h3>
              <h3>your total : <span style="color: #FCBE46;">$</span><span class="cart-total">{{totalCart}}</span></h3>
              <button class="clear-cart banner-btn" @click="clearAll">clear cart</button>
            </div>
          </div>
        </div>
      </div>
    <!-- end cart -->
  </div>
</template>

<script>
import productsList from "../products.js"
import product from "./product.vue"
import cart from "./cart"

export default {
  name: 'HelloWorld',
  components: {
    product,
    cart
  },
  data() {
    return {
      productsList: [],
      showProp: false,
      productsBag: {
        products: [],
        productsId: [],
      },
      productsInCart: {
        items: [],
        products: {
          "id": {
            "1": [],
            "2": [],
            "3": [],
            "4": [],
            "5": [],
            "6": [],
            "7": [],
            "8": [],
          }
        },
        total: 0,
      }
    }
  },
  created() {
    //do something after creating vue instance
    this.productsList = productsList()
    let cartStorage = JSON.parse(localStorage.getItem('cart' || '[]'));
    let productsInlocalStorate = JSON.parse(localStorage.getItem('productsBag' || '[]'));

    if(productsInlocalStorate === null) {
      this.productsBag.products = []
    }else {
      this.productsBag.products = JSON.parse(localStorage.getItem('productsBag' || '[]'));
    }

    if(this.productsBag.products.length > 0 ) {
      this.productsBag.products.forEach(obj => {
        this.productsBag.productsId.push(obj.sys.id)
      })
    }

    if(cartStorage != null && Object.values(cartStorage.id).length > 0) {
      this.productsInCart.products.id["1"] = cartStorage.id["1"]
      this.productsInCart.products.id["2"] = cartStorage.id["2"]
      this.productsInCart.products.id["3"] = cartStorage.id["3"]
      this.productsInCart.products.id["4"] = cartStorage.id["4"]
      this.productsInCart.products.id["5"] = cartStorage.id["5"]
      this.productsInCart.products.id["6"] = cartStorage.id["6"]
      this.productsInCart.products.id["7"] = cartStorage.id["7"]
      this.productsInCart.products.id["8"] = cartStorage.id["8"]
    }

  },
  methods: {
    showCart() {
      this.showProp = !this.showProp;
    },
    closeCart() {
      this.showProp = !this.showProp;
    },
    selectedProduct(index) {

      if(this.productsBag.products.length === 0) {
        this.productsBag.products.push(this.productsList[index])
        this.productsBag.productsId.push(this.productsList[index].sys.id)
        localStorage.setItem("productsBag", JSON.stringify(this.productsBag.products));

        this.productsInCart.products.id[this.productsList[index].sys.id].push(this.productsList[index])
        localStorage.setItem("cart", JSON.stringify(this.productsInCart.products));

      }else if(this.productsBag.productsId.indexOf(this.productsList[index].sys.id) < 0) {
        this.productsBag.products.push(this.productsList[index])
        this.productsBag.productsId.push(this.productsList[index].sys.id)
        localStorage.setItem("productsBag", JSON.stringify(this.productsBag.products));

        this.productsInCart.products.id[this.productsList[index].sys.id].push(this.productsList[index])
        localStorage.setItem("cart", JSON.stringify(this.productsInCart.products));
      }
    },
    addNewItem(obj) {
      this.productsInCart.products.id[obj.sys.id].push(obj)
      localStorage.setItem("cart", JSON.stringify(this.productsInCart.products));
    },
    removeAnItem(obj) {

      this.productsInCart.products.id[obj.sys.id].splice(0,1)
      localStorage.setItem("cart", JSON.stringify(this.productsInCart.products));


      // if(this.productsInCart.products.id[obj.sys.id].length <= 0) {
      //   // console.log(this.productsBag.productsId.indexOf(obj.sys.id))
      //   this.productsBag.products.splice(this.productsBag.productsId.indexOf(obj.sys.id), 1)
      //   this.productsBag.productsId.splice(this.productsBag.productsId.indexOf(obj.sys.id), 1)
      //   localStorage.setItem("productsBag", JSON.stringify(this.productsBag.products));
      // }
    },
    // deleteItem(index) {
    //
    //   let idItem = this.productsBag.products[index].sys.id
    //   this.productsInCart.products.id[idItem].splice(0)
    //   localStorage.setItem("cart", JSON.stringify(this.productsInCart.products));
    //
    //   let indexOfIdInProductsId = this.productsBag.productsId.indexOf(this.productsBag.products[index].sys.id)
    //   this.productsBag.products.splice(indexOfIdInProductsId, 1)
    //   this.productsBag.productsId.splice(indexOfIdInProductsId, 1)
    //   localStorage.setItem("productsBag", JSON.stringify(this.productsBag.products));
    //
    // },
    clearAll() {
      if(this.productsBag.products.length > 0) {
        this.productsBag.products.splice(0)
        this.productsBag.productsId.splice(0)
        localStorage.setItem("productsBag", JSON.stringify(this.productsBag.products));
      }

      if(this.productsInCart.items > 0) {
        // console.log("se cumple")
        let clearItemsInCart = function(array) {
          return array.splice(0)
        }
        let totalIdsInCart = Object.keys(this.productsInCart.products.id).length
        for(let i=1; i<= totalIdsInCart; i++) {
          clearItemsInCart(this.productsInCart.products.id[i])
        }
        localStorage.setItem("cart", JSON.stringify(this.productsInCart.products));
      }
    }
  },
  computed: {
    totalItems() {
      return this.productsInCart.items = [
        Object.values(this.productsInCart.products.id["1"]).length,
        Object.values(this.productsInCart.products.id["2"]).length,
        Object.values(this.productsInCart.products.id["3"]).length,
        Object.values(this.productsInCart.products.id["4"]).length,
        Object.values(this.productsInCart.products.id["5"]).length,
        Object.values(this.productsInCart.products.id["6"]).length,
        Object.values(this.productsInCart.products.id["7"]).length,
        Object.values(this.productsInCart.products.id["8"]).length,
      ].reduce((acc, current) => {
        return acc + current
      }, 0)
    },
    totalCart() {

      let sumArrayValue = function(array) {
        let total = 0;
        array.forEach(obj => {
          total += obj.fields.price
        })
        return total
      }
      let totalCartArray = [
        sumArrayValue(Object.values(this.productsInCart.products.id["1"])),
        sumArrayValue(Object.values(this.productsInCart.products.id["2"])),
        sumArrayValue(Object.values(this.productsInCart.products.id["3"])),
        sumArrayValue(Object.values(this.productsInCart.products.id["4"])),
        sumArrayValue(Object.values(this.productsInCart.products.id["5"])),
        sumArrayValue(Object.values(this.productsInCart.products.id["6"])),
        sumArrayValue(Object.values(this.productsInCart.products.id["7"])),
        sumArrayValue(Object.values(this.productsInCart.products.id["8"]))
      ]

      return totalCartArray.reduce((acc, current) => {
        return acc + current
      }, 0).toFixed(2)
    }
  }
}
</script>

