<template lang="html">
  <div class="cart-item">
    <img :src="cartObject.fields.image.fields.file.url" alt="product">
    <div>
      <h4>{{cartObject.fields.title}}</h4>
      <h5>{{cartObject.fields.price | showPriceItem}}</h5>
      <!-- <span class="remove-item" @click="deleteItem()">remove</span> -->
    </div>
    <div>
      <font-awesome-icon icon="chevron-up" @click="addItem()"></font-awesome-icon>
      <p class="item-amount">{{items}}</p>
      <font-awesome-icon icon="chevron-down" @click="removeItem()"></font-awesome-icon>
    </div>
  </div>
</template>

<script>
export default {
  name: 'cart',
  data() {
    return {
      items: 0,
    }
  },
  props: {
    cartObject: {
      type: Object,
      required: true,
    },
    cartIndexObject: {
      type: Number,
      required: true,
    },
  },
  created() {
    let itemsInCartById = JSON.parse(localStorage.getItem('cart' || '[]'));
    if(itemsInCartById != null && itemsInCartById.id[this.cartObject.sys.id].length != 0) {
      this.items = itemsInCartById.id[this.cartObject.sys.id].length
    }
  },
  methods: {
    addItem() {
      this.$emit('increaseItem', this.cartObject)
      this.items ++
    },
    removeItem() {
      this.$emit('reduceItem', this.cartObject)
      if(this.items > 0 ) {
        this.items--
      }
    },
    // deleteItem() {
    //   this.$emit('deleteItemInCart', this.cartIndexObject)
    // }
  },
  filters: {
    showPriceItem(value) {
      return value = value.toFixed(2)
    }
  }
}
</script>

